.ldsEllipsis {
   display: flex;
   justify-content: center;
   padding-top: 100px;
   position: relative;
   height: calc(100vh - 64px);
 }
 .wrapDot {
   display: block;
   position: relative;
   width: 80px;
   height: 80px;
 }
 .ldsEllipsis .wrapDot div {
   position: absolute;
   top: 33px;
   width: 13px;
   height: 13px;
   border-radius: 50%;
   background: gray;
   animation-timing-function: cubic-bezier(0, 1, 1, 0);
 }
 .ldsEllipsis .wrapDot div:nth-child(1) {
   left: 8px;
   animation: ldsEllipsis1 0.6s infinite;
 }
 .ldsEllipsis .wrapDot div:nth-child(2) {
   left: 8px;
   animation: ldsEllipsis2 0.6s infinite;
 }
 .ldsEllipsis .wrapDot div:nth-child(3) {
   left: 32px;
   animation: ldsEllipsis2 0.6s infinite;
 }
 .ldsEllipsis .wrapDot div:nth-child(4) {
   left: 56px;
   animation: ldsEllipsis3 0.6s infinite;
 }
 @keyframes ldsEllipsis1 {
   0% {
     transform: scale(0);
   }
   100% {
     transform: scale(1);
   }
 }
 @keyframes ldsEllipsis3 {
   0% {
     transform: scale(1);
   }
   100% {
     transform: scale(0);
   }
 }
 @keyframes ldsEllipsis2 {
   0% {
     transform: translate(0, 0);
   }
   100% {
     transform: translate(24px, 0);
   }
 }
 